<template>
  <div class="logintem">
    <div class="title">
      <!-- <img src="../assets/hp.png"> -->
      <img src="data:image/png;base64,">
    </div>
    <el-form :model="loginForm" :rules="rules" ref="loginForm" label-width="0px">
      <div class="logincontent">
        <div class="infologin">
          用户登录
        </div>
        <el-form-item prop="username">
          <el-input v-model="loginForm.username" placeholder="请输入帐号" class="inputset inputuser" @keyup.enter.native="submitInfo" ></el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input type="password" v-model="loginForm.password" auto-complete="off" placeholder="请输入密码" class="inputset inputpsd" @keyup.enter.native="submitInfo"></el-input>
        </el-form-item>
        <el-button class="slictebtn" @click="submitInfo">登录</el-button>
      </div>
    </el-form>
  </div>
</template>
<script>
  // import { mapActions } from 'vuex'
  export default {
    data(){
      /* 自定义校验规则start */
      var checkname = (rule, value, callback) => {        
          if(!value){
            callback(new Error('请输入帐号'));
          }
          if (value.length > 20) {
             callback(new Error('请输入不超过20位的账户名'));
          } 
          var pattern = /^\w+$/;    
          if (!pattern.test(value)) {
            callback(new Error('请输入含有字母和数字的账户名'));
          } 
          return callback(); 
      };
      var checkpsd = (rule, value, callback) => {        
          if(!value){
            callback(new Error('请输入密码'));
          }
          if (value.length > 20||value.length < 6) {
             callback(new Error('请输入最少6位，最多20位的密码'));
          } 
          var pattern = /^\w+$/;    
          if (!pattern.test(value)) {
            callback(new Error('请输入含有字母和数字的账户名'));
          } 
          return callback();
      };
      /*自定义校验规则 end */
      return {
        loginForm: {
          username: '',
          password: ''
        },
        rules: {
          username:[
            {validator: checkname, trigger: 'blur'}
          ],
          password:[
            {validator: checkpsd, trigger: 'blur'}
          ]
        }
      }
    },
    created() {
        this.loginForm = this.$store.state.users.loginForm;
        if(!sessionStorage.getItem('id')) {
        	this.loginForm.username = '';
        	this.loginForm.password = '';
        }
    },   
    // beforeDestory() {
    //     this.$store.state.users.loginForm = this.loginForm;
    // },   
    methods: {
      submitInfo() {
        this.$refs.loginForm.validate((valid) => {          
          if(valid) {
              var parameterData = {         
                  username: this.loginForm.username,
                  password: this.loginForm.password            
              };                 
              // this.login("login", parameterData);
              // this.$store.dispatch("login", parameterData);
              this.$http.post('/login', parameterData, {emulateJSON:true}).then(                
	          (res) => {
	              if(res.data.result == 'ok') { 
	                //router.replace("record");
	                this.$router.replace("record");       
	              } else {                
	                this.$message({type:'error', message: res.data.msg, duration:2000});
	              }                 
	          }, (res) => {

	          });
          } else {
              return false;           
          }
        })
      }
      // ,
      // ...mapActions([
      //     'login' //映射 this.login() 到 this.$store.dispatch('login')
      // ])
    }
  }
</script>
<style>
  .logintem{
    position: absolute;
    top:0;
    bottom: 0;
    left: 0;
    right: 0;
    background-image: url("data:image/jpg;base64,");
    -webkit-background-size:100% 100%;
    background-size:100% 100%;
    /*font-family: 微软雅黑;*/
    font-family: \5FAE\8F6F\96C5\9ED1,Arial,sans-serif;
  }
  .logintem .title{
    font-size: 44px;
    color: #fff;
    margin: 150px auto 50px;
    font-weight: 600;
    text-align: center;
  }
  .logincontent{
    display: block;
    width:350px;
    height:180px;
    background-color: #fff;
    margin:0 auto 0;
    padding: 0 50px 80px;
    border-radius: 4px;
  }
  .infologin{
    font-size: 20px;
    color: #414fda;
    padding-top: 20px;
    margin-bottom: 20px;
    font-weight: 700;
  }
  .slictebtn{
    width:100%;
    height:40px;
    color: #fff;
    background-color: #414fda;
    margin-top: 10px;
    font-size: 20px;
    font-family: \5FAE\8F6F\96C5\9ED1,Arial,sans-serif;
  }
  .slictebtn.el-button, .slictebtn.el-button:hover,
  .slictebtn.el-button:active{
    background-color: #5367DA;
    color: #fff;
  }
  .logincontent .inputset{
    background-repeat:no-repeat;
    border: 1px solid #C0CCDA ;
    border-radius: 5px;
  }
  .logincontent .inputset.inputuser{
    background-position: 10px 8px;
    background-image: url("../assets/user.png");
  }
  .logincontent .inputset.inputpsd{
    background-position: 13px 8px;
    background-image: url("../assets/psd.png");
  }
  .logincontent .inputset input{
    background-color: transparent;
    width:310px;
    margin-left: 38px;
    border: none;
  }
</style>


