 <template>
  <div class="tophead">
    <div class="logo-img">
      <img src="../assets/logo.png">
    </div>
    <div class="logo">      
      个人身份人证识别信息管理系统
    </div>
    <div class="user-info">
      <div class="user-cur">
        <img src="../assets/account.png">
        <div class="user-name">{{userData.userName}}</div>
      </div>
      <div class="user-oper">
        <ul>
          <li class="user-oper-li" @click="showDailog">
            <div class="oper-icon-1 oper-icon-base"></div>
            <div class="oper-txt">修改密码</div>
          </li>
          <li class="user-oper-li" @click="quitajax">
            <div class="oper-icon-2 oper-icon-base"></div>
            <div class="oper-txt">退出</div>
          </li>
        </ul>
      </div>
    </div>
    <el-dialog title="修改密码" v-model="dialogFormVisible" size="tiny">
      <el-form :model="psdForm" :rules="psdFormrules" ref="psdForm">
        <el-form-item label="原始密码" :label-width="formLabelWidth" prop="oldpsd">
          <el-input v-model="psdForm.oldpsd" auto-complete="off" type="password" placeholder="请输入原始密码"></el-input>
        </el-form-item>
        <el-form-item label="新密码" :label-width="formLabelWidth" prop="newpsd">
          <el-input v-model="psdForm.newpsd" auto-complete="off" type="password" placeholder="请输入新密码"></el-input>
        </el-form-item>
        <el-form-item label="确认新密码" :label-width="formLabelWidth" prop="surepsd">
          <el-input v-model="psdForm.surepsd" auto-complete="off" type="password" placeholder="请重复输入新密码"></el-input>
        </el-form-item>
        <div class="dialog-footer">
          <el-button type="primary" @click="changepsdajax" :disabled="saveDisabled">确 定</el-button>
          <el-button @click="dialogFormVisible = false">取 消</el-button>
        </div>
      </el-form>    
    </el-dialog>
  </div>
</template>

<script>
  import { mapState } from 'vuex' 
  import { mapGetters } from 'vuex'

  export default {
    name: 'tophead',
    data () {
      /* 自定义校验规则start */
      var psdvlidata = (rule, value, callback) => {       
          if(!value){
             callback(new Error('请输入原始密码'));
          }
          if (value.length > 20 || value.length < 6) {
             callback(new Error('请输入大于5位，小于20位的原始密码'));
          } 
          var pattern = /^\w+$/;    
          if (!pattern.test(value)) {
             callback(new Error('密码名只含有字母、数字和下划线'));
          } 
          return callback(); 
      };
      var psdnewvlidata = (rule, value, callback) => {       
          if(!value){
             callback(new Error('请输入新密码'));
          }
          if (value.length > 20 || value.length < 6) {
             callback(new Error('请输入大于5位，小于20位的原始密码'));
          } 
          var pattern = /^\w+$/;    
          if (!pattern.test(value)) {
             callback(new Error('密码名只含有字母、数字和下划线'));
          } 
          return callback(); 
      };
      var secdpsd = (rule, value, callback) => {
          if (!value) {
            callback(new Error('请重复输入新密码'));
          } else if (value !== this.psdForm.newpsd) {
            callback(new Error('两次输入密码不一致'))
          } else {
            return callback();
          }
      };
      /* 自定义校验规则 end */

      return {
          dialogFormVisible: false,
          formLabelWidth: '100px',
          saveDisabled: 'false',
          psdForm: {
            oldpsd: '',
            newpsd: '',
            surepsd: ''
          },         
          psdFormrules: {
            oldpsd: [
              {validator: psdvlidata, trigger: 'blur'}
            ],
            newpsd: [
              {validator: psdnewvlidata, trigger: 'blur'}
            ],
            surepsd: [
              {validator: secdpsd, trigger: 'blur'}
            ]
          }
        }
    },     
    created() {
        this.$store.dispatch("userInfo");  
        // this.userInfo();     
        // this.userInfo = this.$store.state.users.userInfo;         
    },    
    beforeDestory() {
        // this.$store.state.users.userInfo = this.userInfo;
    },     
    methods: {
        showDailog() {
            this.dialogFormVisible = true;           
        },       
        changepsdajax() {            
            this.$refs.psdForm.validate((valid) => {
                if (valid) {                                       
                    var parameterData = {         
                        id: this.userData.id,
                        password: this.psdForm.newpsd,
                        oldpsd: this.psdForm.oldpsd            
                    }; 
           
                    this.$http.post('/user/updatePwd.json', parameterData, {emulateJSON:true}).then(
                       (res) => {
                          if(res.data.result == 'ok') {                              
                              this.dialogFormVisible = false;
                              this.$message({type: 'success', message: '密码修改成功'});
                           } else {
                              this.$message({type: 'error', message: res.data.msg, duration: 2000});
                           }                 
                        }, (res) => {
                           this.$message({type: 'error', message: '密码修改失败,请稍候再试', duration: 2000});
                        }
                    )    
                } else {               
                    return false;
                }
            });
        },
        quitajax() {
           this.$confirm('你确定退出吗?', '提示', {
               confirmButtonText: '确定',
               cancelButtonText: '取消',
               type: 'warning'
           }).then(function () {
               this.$store.dispatch("logOut");               
           }.bind(this)).catch(() => {
           });
        }         
    },    
    computed:{
        // ...mapGetters({
        //     userData: 'getUserInfo'
        // })       

        // ...mapState({
        //     userData: state => state.users.userInfo
        // })  

        userData: function () {          
           return this.$store.state.users.userInfo   //获取vuex里面 state 状态值
        }

        // ...mapActions([
        //   'userInfo' //映射 this.userInfo() 到 this.$store.dispatch('userInfo')
        // ])
    },
    watch: {     
      dialogFormVisible() {
          if(!this.dialogFormVisible) {     
             this.saveDisabled = true;
             this.$refs.psdForm.resetFields();
          } else {
             this.saveDisabled = false;
          }
      },
      userData() {
          if(this.userData) {           
            // alert(this.userData.userName);
          }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .tophead {
    background: #4776c8;
    overflow: hidden;
  }

  .logo-img {
    float: left;
    margin-left: 55px;
    margin-top: 30px;
  }

  .logo {
    float: left;
    font-size: 26px;
    color: #fff;
    height: 100px;
    line-height: 100px;
    margin-left: 20px;    
  }

  .user-info {
    float: right;
  }

  .user-oper {
    width: 230px;
    height: 100px;
    background: #587ed1;
  }

  .user-oper ul {
    margin-left: 15px;
  }

  .user-oper ul li {
    float: left;
    width: 100px;
    height: 100px;
    text-align: center;
    font-size: 16px;
  }
  .oper-icon-base{
    background: url("data:image/png;base64,") no-repeat;
  }
  .oper-icon-1 {
    height: 70px;
    background-position: -161px -930px;
  }

  .oper-icon-2 {
    height: 70px;
    background-position: -161px -1040px;
  }

  .user-oper-li {
    color: #d0e8f9;
  }

  .user-oper-li:hover {
    cursor: pointer;
    color: #fff;
  }

  .user-oper-li:hover .oper-icon-1 {
    background-position: -55px -930px;
  }

  .user-oper-li:hover .oper-icon-2 {
    background-position: -55px -1040px;
  }

  .user-cur {
    float: left;
    padding: 20px 50px 0;
    text-align: center;
  }

  .user-name {
    color: #fff;
    font-size: 16px;
    font-weight: bold;
  }

  .user-oper {
    float: left;
    display: inline-block;
  }
</style>



